<template>
  <svg
    class="icon"
    v-bind:class="{'left': position == 'left', 'right': position == 'right', 'spinning': spinning == true}"
    aria-hidden="true"
    focusable="false"
  >
    <use v-bind="{'xlink:href' : '/static/symbol-defs.svg#icon-' + iconRef + '-outline'}"></use>
  </svg>
</template>

<script>
export default {
  name: "base-icon",
  props: {
    iconRef: {
      default: "question-mark-circle",
      type: String
    },
    spinning: {
      default: false,
      type: Boolean
    },
    position: {
      default: "center",
      type: String
    }
  },
  data: function() {
    return {};
  }
};
</script>

<style>
</style>



