<template>
  <input
    class="input"
    :type="type"
    :placeholder="placeholder"
    v-bind:value="value"
    @input="$emit('input', $event.target.value)"
    @keyup.enter="$emit('enter', $event.target.value)"
  >
</template>

<script>
export default {
  name: "",
  props: {
    value: "",
    type: {
      default: "text",
      type: String
    },
    placeholder: {
      default: "",
      type: String
    }
  },
  data: function() {
    return {};
  }
};
</script>

<style>
</style>



