<template>
  <div class="modal-box">
    <div class="modal-title">
      <h3>
        <base-icon iconRef="archive" position="left"/>Bewerk project
      </h3>
      <div class="flex-divider"></div>
      <base-button type="secondary" @click="closeModal">
        <base-icon iconRef="close"/>
      </base-button>
    </div>
    <div class="modal-content">
      <div class="modal-content-section">
        <div class="input-header">
          <h4>Basisgegevens</h4>
        </div>
        <div class="input-container">
          <label class="label" for>Projectnaam</label>
          <base-input type="text" v-model="projectBody.projectName"/>
        </div>
        <div class="input-container">
          <label for class="label">Beschrijving</label>
          <textarea class="input" v-model="projectBody.description"></textarea>
        </div>
      </div>
      <div class="modal-content-section">
        <div class="input-header">
          <h4>Adres</h4>
        </div>
        <div class="input-row">
          <div class="input-container">
            <label class="label">Straat</label>
            <base-input type="text" v-model="projectBody.address.street"/>
          </div>
          <div class="input-container">
            <label class="label">Huisnummer</label>
            <base-input type="text" v-model="projectBody.address.houseNumber"/>
          </div>
        </div>
        <div class="input-container">
          <label class="label">Stad</label>
          <base-input type="email" v-model="projectBody.address.city"/>
        </div>
        <div class="input-container">
          <label class="label">Postcode</label>
          <base-input type="tel" v-model="projectBody.address.postCode"/>
        </div>
      </div>
    </div>
    <div class="modal-footer">
      <base-button @click="deleteProject" type="danger">Verwijderen</base-button>
      <div class="flex-divider"></div>
      <base-button @click="closeModal" type="secondary">Annuleren</base-button>
      <base-button @click="editProject">
        <base-icon iconRef="checkmark" position="left"/>Bewerk
      </base-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "project-edit-modal",
  data: function() {
    return {
      projectBody: {
        projectName: "",
        description: "",
        address: {
          street: "",
          houseNumber: "",
          city: "",
          postCode: ""
        }
      }
    };
  },
  computed: {
    clientId: function() {
      return this.$store.getters.getCurrentClient.id;
    },
    projectData: function() {
      return this.$store.getters.getCurrentProject.data();
    }
  },
  methods: {
    editProject: function() {
      const projectData = {
        id: this.$route.params.projectId,
        body: this.$editProjectDataObject(this.projectBody)
      };
      this.$store.dispatch("editProject", projectData);
      this.closeModal();
    },
    deleteProject: function() {
      const projectData = {
        id: this.$route.params.projectId,
        body: this.$deleteProjectDataObject(),
        deleting: true
      };
      this.$store.dispatch("editProject", projectData);
      this.closeModal();
      this.$router.push(`/klant/${this.clientId}`);
    },
    closeModal: function() {
      this.$store.commit("setModalShown", {
        dataType: "project",
        modalType: "edit",
        shown: false
      });
    }
  },
  mounted: function() {
    this.projectBody.projectName = this.projectData.projectName;
    this.projectBody.description = this.projectData.description;
    this.projectBody.address.street = this.projectData.address.street;
    this.projectBody.address.houseNumber = this.projectData.address.houseNumber;
    this.projectBody.address.city = this.projectData.address.city;
    this.projectBody.address.postCode = this.projectData.address.postCode;
  }
};
</script>



