import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

import Home from "../components/Home.vue";
import Login from "../components/Login.vue";
import Clients from "../components/Clients.vue";
import Client from "../components/Client.vue";
import Project from "../components/Project.vue";
import Room from "../components/Room.vue";
import Products from "../components/Products.vue";

export const router = new VueRouter({
  routes: [
    {
      path: "/login",
      name: "Login",
      component: Login
    },
    {
      path: "/",
      name: "Home",
      component: Home,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/klanten",
      name: "Klanten",
      component: Clients,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/klant/:clientId",
      name: "Klant",
      component: Client,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/project/:projectId",
      name: "Project",
      component: Project,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/ruimte/:roomId",
      name: "Ruimte",
      component: Room,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/producten",
      name: "Producten",
      component: Products,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "*",
      redirect: "/"
    }
  ]
});



