<template>
  <header class="header">
    <div class="header-container">
      <div class="header-title-container">
        <router-link to="/">
          <h2 class="heading">
            <img src="/static/favicons/android-chrome-192x192.png" alt="Sonar Logo"> Sonar
          </h2>
        </router-link>
      </div>
      <div class="flex-divider"></div>
      <div class="account-container dropdown-container" v-if="userLoggedIn">
        <base-button class="dropdown-label" :type="'secondary'">Account
          <base-icon iconRef="chevron-down" position="right"/>
        </base-button>
        <ul class="dropdown-list">
          <li class="dropdown-item">
            <base-button id="log-out-button" :type="'secondary'" :loading="false" @click="logOut">
              <base-icon iconRef="log-out" position="left"/>Log uit
            </base-button>
          </li>
        </ul>
      </div>
    </div>
  </header>
</template>

<script>
import firebase from "firebase";
import Icon from "./Icon.vue";

export default {
  name: "Navbar",
  components: {
    Icon
  },
  data: function() {
    return {};
  },
  computed: {
    userLoggedIn() {
      return this.$store.getters.getUser !== null;
    },
    currentPageName() {
      return this.$store.getters.getCurrentPage.name;
    },
    parentPage() {
      return this.$store.getters.getParentPage;
    }
  },
  methods: {
    logOut: function() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$store.dispatch("setUser");
          this.$router.replace("login");
        });
    }
  }
};
</script>

<style>
</style>



