<template>
  <div class="data-table-wrapper">
    <table class="page-datatable" v-if="dataList.length != 0">
      <thead>
        <tr>
          <th>
            <span>Ruimtenaam</span>
            <base-icon iconRef="chevron-down" position="right"/>
          </th>
          <th>
            <span>Oppervlakte</span>
            <base-icon iconRef="chevron-down" position="right"/>
          </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="dataEntry in dataList" :key="dataEntry.id" @click="$emit('click', dataEntry.id)">
          <td>
            <span>{{dataEntry.data().roomName}}</span>
          </td>
          <td>
            <span>
              {{area(dataEntry.data().length, dataEntry.data().width)}} m
              <sup>2</sup>
            </span>
          </td>
        </tr>
      </tbody>
    </table>
    <p class="page-datatable-disclaimer" v-if="dataList.length == 0">Er zijn geen ruimtes gevonden.</p>
  </div>
</template>

<script>
export default {
  name: "rooms-data-list",
  props: {
    dataList: {
      type: Array
    }
  },
  methods: {
    area: function(length, width) {
      return Number(length) * Number(width);
    }
  }
};
</script>

<style>
</style>



