<template>
  <svg :class="'icon ' + position"  aria-hidden="true" focusable="false">
    <use v-bind="{'xlink:href' : '/static/symbol-defs.svg#icon-' + iconRef}"></use>
  </svg>
</template>

<script>
export default {
  name: "Icon",
  props: {
    iconRef: {
      type: String
    },
    position: {
      type: String
    }
  },
  data: function() {
    return {};
  }
};
</script>

<style>
</style>



