<template>
  <div class="acousticsscore-chart">
    <h4>Rivasono Akoestiek Score</h4>
    <div class="boxes">
      <div class="box label-box">
        <div class="score-label">Huidige akoestiek</div>
        <div class="score-label">Na toepassen producten</div>
      </div>
      <div class="box score-box">
        <div class="score"><span
            id="acousticsScore"
            class="score-value"
          >{{acousticsScore}}</span> / 10</div>
        <div class="score"><span
            id="acousticsScoreWithProducts"
            class="score-value"
          >{{acousticsScoreWithProducts}}</span> / 10</div>
      </div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
.acousticsscore-chart {
  padding: 0 24px;
  display: flex;
  flex-direction: column;
}
h4 {
  margin-top: 32px;
  font-size: 100%;
  text-align: center;
}
.boxes {
  margin-top: 8px;
  display: flex;
  justify-content: center;
  flex-grow: 1;
  flex-basis: 0;
}
.box {
  display: flex;
  flex-direction: column;
  width: 50%;
}
.label-box {
  margin-right: 12px;
  text-align: right;
}
.score-box {
  margin-left: 12px;
}
.score-label:first-child,
.score:first-child {
  margin-bottom: 16px;
}
.score {
  font-size: 20px;
}
.score-value {
  font-size: 32px;
  font-weight: bold;
  color: #0a475f;
}
</style>

<script>
import CountUp from "countup.js";
export default {
  props: ["acousticsScores"],
  data: function() {
    return {
      acousticsScoreAnimation: "",
      acousticsScoreWithProductsAnimation: ""
    };
  },
  computed: {
    acousticsScore: function() {
      return Math.round(this.acousticsScores[0]) / 10;
    },
    acousticsScoreWithProducts: function() {
      const value = Math.round(this.acousticsScores[1]) / 10;
      return value >= 10 ? 10 : value.toFixed(1);
    }
  },
  watch: {
    acousticsScoreWithProducts: function() {
      this.acousticsScoreWithProductsAnimation.update(
        this.acousticsScoreWithProducts
      );
    }
  },
  mounted: function() {
    const acousticsScoreDOM = document.querySelector("#acousticsScore");
    const acousticsScoreWithProductsDOM = document.querySelector(
      "#acousticsScoreWithProducts"
    );
    this.acousticsScoreAnimation = new CountUp(
      acousticsScoreDOM,
      0,
      this.acousticsScore,
      1
    );
    this.acousticsScoreWithProductsAnimation = new CountUp(
      acousticsScoreWithProductsDOM,
      0,
      this.acousticsScoreWithProducts,
      1
    );
    this.acousticsScoreAnimation.start();
    this.acousticsScoreWithProductsAnimation.start();
  }
};
</script>



