<template>
  <div class="page-container">
    <base-breadcrumbs
      :parents="[['Home','/'], ['Klanten', '/klanten']]"
      :currentLocation="currentClient.data().clientName"
    />
    <div class="page-box accent">
      <div class="page-box-title">
        <h2>
          <base-icon
            iconRef="person"
            position="left"
          />
          {{currentClient.data().clientName}}
        </h2>
        <div class="flex-divider"></div>
        <base-button
          type="secondary"
          @click="openEditModal"
        >
          <base-icon
            iconRef="edit"
            position="left"
          />Bewerk
        </base-button>
      </div>
      <div class="page-box-content values">
        <div class="value-group">
          <div class="label">Beschrijving</div>
          <div class="value">{{currentClient.data().description}}</div>
        </div>
        <div class="value-group">
          <div class="label">Contactpersoon</div>
          <div class="value">{{`${currentClient.data().contact.firstName} ${currentClient.data().contact.lastName}`}}</div>
        </div>
      </div>
      <div class="page-box-footer"></div>
    </div>
    <div class="page-box">
      <div class="page-box-title">
        <h3>
          <base-icon
            iconRef="archive"
            position="left"
          />Projecten
        </h3>
        <div class="flex-divider"></div>
        <base-button @click="openCreateModal">
          <base-icon
            iconRef="plus"
            position="left"
          />Nieuw Project
        </base-button>
      </div>
      <div class="page-box-datatable-container">
        <projects-data-list
          :dataList="currentClientProjectsList"
          @click="dataEntryClick($event)"
        />
      </div>
    </div>
  </div>
</template>

<script>
import projectsDataList from "./data-list-components/projects-data-list.vue";

export default {
  name: "Client",
  components: {
    projectsDataList
  },
  computed: {
    currentClient: function() {
      return this.$store.getters.getClientsList.find(client => {
        return client.id == this.$route.params.clientId;
      });
    },
    currentClientProjectsList: function() {
      return this.$store.getters.getProjectsList.filter(project => {
        return (project.data().belongsTo =
          this.currentClient.id && project.data().trashed == false);
      });
    }
  },
  methods: {
    openEditModal: function() {
      this.$store.commit("setModalShown", {
        dataType: "client",
        modalType: "edit",
        shown: true
      });
    },
    openCreateModal: function() {
      this.$store.commit("setModalShown", {
        dataType: "project",
        modalType: "create",
        shown: true
      });
    },
    dataEntryClick: function(value) {
      this.openProject(value);
    },
    openProject: function(targetProjectId) {
      this.$router.push(`/project/${targetProjectId}`);
    }
  },
  created: function() {
    this.$setPageTitle(`Klant - ${this.currentClient.data().clientName}`);
  }
};
</script>

<style>
</style>



