<template>
  <div class="modal-box">
    <div class="modal-title">
      <h3>
        <base-icon iconRef="activity" position="left"/>Nieuwe Meting
      </h3>
      <div class="flex-divider"></div>
      <base-button type="secondary" @click="closeModal">
        <base-icon iconRef="close"/>
      </base-button>
    </div>
    <div class="modal-content">
      <div class="modal-content-section">
        <div class="input-header">
          <h4>Basisgegevens</h4>
        </div>
        <div class="input-container">
          <label class="label">Metingtype</label>
          <select
            class="input"
            v-bind:value="newMeasurementData.type"
            @change="newMeasurementData.type = $event.target.value"
          >
            <option value="Nulmeting">Nulmeting</option>
            <option value="Tussenmeting">Tussenmeting</option>
            <option value="Eindmeting">Eindmeting</option>
          </select>
        </div>
        <div class="input-container">
          <label class="label">Label</label>
          <base-input type="text" placeholder="Label" v-model="newMeasurementData.label"/>
        </div>
        <div class="input-container">
          <label for class="label">Beschrijving</label>
          <textarea
            class="input"
            rows="4"
            placeholder="Beschrijving"
            v-model="newMeasurementData.description"
          ></textarea>
        </div>
        <div class="input-row">
          <div class="input-container">
            <label class="label">Meting door</label>
            <base-input type="text" placeholder="Naam" v-model="newMeasurementData.author"/>
          </div>
          <div class="input-container">
            <label class="label">Datum</label>
            <base-input type="text" placeholder="01-01-2019" v-model="newMeasurementData.date"/>
          </div>
        </div>
      </div>
      <div class="modal-content-section">
        <div class="input-header">
          <h4>Meetwaarden</h4>
        </div>
        <div class="input-row">
          <div class="input-container">
            <label class="label">125 Hz</label>
            <base-input type="number" placeholder="0" v-model="newMeasurementData.hz125"/>
          </div>
          <div class="input-container">
            <label class="label">250 Hz</label>
            <base-input type="number" placeholder="0" v-model="newMeasurementData.hz250"/>
          </div>
          <div class="input-container">
            <label class="label">500 Hz</label>
            <base-input type="number" placeholder="0" v-model="newMeasurementData.hz500"/>
          </div>
        </div>
        <div class="input-row">
          <div class="input-container">
            <label class="label">1000 Hz</label>
            <base-input type="number" placeholder="0" v-model="newMeasurementData.hz1000"/>
          </div>
          <div class="input-container">
            <label class="label">2000 Hz</label>
            <base-input type="number" placeholder="0" v-model="newMeasurementData.hz2000"/>
          </div>
          <div class="input-container">
            <label class="label">4000 Hz</label>
            <base-input type="number" placeholder="0" v-model="newMeasurementData.hz4000"/>
          </div>
        </div>
      </div>
    </div>
    <div class="modal-footer">
      <div class="flex-divider"></div>
      <base-button @click="closeModal" type="secondary">Annuleren</base-button>
      <base-button @click="createMeasurement" :loading="pageLoading" :disabled="pageLoading">
        <base-icon iconRef="checkmark" position="left"/>Aanmaken
      </base-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "room-create-modal",
  data: function() {
    return {
      newMeasurementData: {
        measurementName: "",
        description: "",
        type: "",
        label: "",
        author: "",
        date: "",
        hz125: "",
        hz250: "",
        hz500: "",
        hz1000: "",
        hz2000: "",
        hz4000: ""
      }
    };
  },
  computed: {
    pageLoading: function() {
      return this.$store.state.loading.page;
    }
  },
  methods: {
    createMeasurement: function() {
      const newMeasurement = this.$createNewMeasurement(
        this.newMeasurementData
      );
      newMeasurement.belongsTo = this.$route.params.roomId;
      this.$store.dispatch("createMeasurement", newMeasurement);
      this.closeModal();
    },
    closeModal: function() {
      this.$store.commit("setModalShown", {
        dataType: "measurement",
        modalType: "create",
        shown: false
      });
    }
  }
};
</script>



