import Vue from "vue";
import Vuex from "vuex";
import Firebase from "firebase";
import { router } from "../router/router";

Vue.use(Vuex);

// Firebase configuration
const firebaseConfig = {
  apiKey: "AIzaSyA6X7Kxa7dajDykCMGU4s2EMLLK5j4Agpw",
  authDomain: "sonar-2-prototype.firebaseapp.com",
  databaseURL: "https://sonar-2-prototype.firebaseio.com",
  projectId: "sonar-2-prototype",
  storageBucket: "sonar-2-prototype.appspot.com",
  messagingSenderId: "335223530722"
};

// Initialize firebase
Firebase.initializeApp(firebaseConfig);
Firebase.firestore().settings({ timestampsInSnapshots: true });

export const store = new Vuex.Store({
  state: {
    isAppLoading: true,
    user: {},
    usersList: [],
    clientsList: [],
    projectsList: [],
    roomsList: [],
    measurementsList: [],
    modals: {
      client: {
        create: {
          shown: false
        },
        edit: {
          shown: false
        }
      },
      project: {
        create: {
          shown: false
        },
        edit: {
          shown: false
        }
      },
      room: {
        create: {
          shown: false
        },
        edit: {
          shown: false
        }
      },
      measurement: {
        create: {
          shown: false
        },
        edit: {
          shown: false
        }
      },
      analysis: {
        view: {
          shown: false
        }
      },
      advice: {
        view: {
          shown: false
        }
      }
    }
  },
  getters: {
    getLoadingStatus: state => {
      return state.isAppLoading;
    },
    getUser: state => {
      return state.user;
    },
    getClientsList: state => {
      return state.clientsList;
    },
    getProjectsList: state => {
      return state.projectsList;
    },
    getRoomsList: state => {
      return state.roomsList;
    },
    getMeasurementsList: state => {
      return state.measurementsList;
    },
    getUsersList: state => {
      return state.usersList;
    },
    getENVList: state => {
      return state.ENVList;
    },
    getModalStates: state => {
      return state.modals;
    }
  },
  mutations: {
    setAppLoading: (state, trueOrFalse) => {
      state.isAppLoading = trueOrFalse;
    },
    setUser: state => {
      state.user = Firebase.auth().currentUser;
    },
    setClients: (state, clientsList) => {
      state.clientsList = clientsList;
    },
    setProjects: (state, projectsList) => {
      state.projectsList = projectsList;
    },
    setRooms: (state, roomsList) => {
      state.roomsList = roomsList;
    },
    setMeasurements: (state, measurementsList) => {
      state.measurementsList = measurementsList;
    },
    setUsers: (state, usersList) => {
      state.usersList = usersList;
    },
    setENV: (state, ENVList) => {
      state.ENVList = ENVList;
    },
    setModalShown: (state, modalData) => {
      state.modals[modalData.dataType][modalData.modalType].shown = modalData.shown;
    },
    setLoading: (state, loadingData) => {
      state.loading[loadingData.dataType] = loadingData.status;
    }
  },
  actions: {
    setUser: ({ commit }) => {
      commit("setUser");
    },
    // Fetching actions
    fetchAllData: ({ commit, dispatch }) => {
      commit("setAppLoading", true);
      const fetchPromises = [
        dispatch("fetchClients"),
        dispatch("fetchProjects"),
        dispatch("fetchRooms"),
        dispatch("fetchMeasurements"),
        dispatch("fetchUsers"),
        dispatch("fetchENV")
      ];
      Promise.all(fetchPromises).then(() => {
        console.log("All fetch promises resolved");
        commit("setAppLoading", false);
      });
    },
    fetchClients: ({ commit }) => {
      return new Promise((resolve, reject) => {
        Firebase.firestore()
          .collection("clients")
          .where("trashed", "==", false)
          .get()
          .then(querySnapshot => {
            const clientsList = [];
            querySnapshot.forEach(doc => {
              clientsList.push(doc);
            });
            commit("setClients", clientsList);
          })
          .then(() => {
            console.log("Done fetching clients");
            resolve();
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    fetchProjects: ({ commit }) => {
      return new Promise((resolve, reject) => {
        Firebase.firestore()
          .collection("projects")
          .where("trashed", "==", false)
          .get()
          .then(querySnapshot => {
            const projectsList = [];
            querySnapshot.forEach(doc => {
              projectsList.push(doc);
            });
            commit("setProjects", projectsList);
          })
          .then(() => {
            resolve();
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    fetchRooms: ({ commit }) => {
      return new Promise((resolve, reject) => {
        Firebase.firestore()
          .collection("rooms")
          .where("trashed", "==", false)
          .get()
          .then(querySnapshot => {
            const roomsList = [];
            querySnapshot.forEach(doc => {
              roomsList.push(doc);
            });
            commit("setRooms", roomsList);
          })
          .then(() => {
            resolve();
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    fetchMeasurements: ({ commit }) => {
      return new Promise((resolve, reject) => {
        Firebase.firestore()
          .collection("measurements")
          .where("trashed", "==", false)
          .get()
          .then(querySnapshot => {
            const measurementsList = [];
            querySnapshot.forEach(doc => {
              measurementsList.push(doc);
            });
            commit("setMeasurements", measurementsList);
          })
          .then(() => {
            resolve();
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    fetchUsers: ({ commit }) => {
      return new Promise((resolve, reject) => {
        Firebase.firestore()
          .collection("users")
          .get()
          .then(querySnapshot => {
            const usersList = [];
            querySnapshot.forEach(doc => {
              usersList.push(doc);
            });
            commit("setUsers", usersList);
          })
          .then(() => {
            resolve();
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    fetchENV: ({ commit }) => {
      return new Promise((resolve, reject) => {
        Firebase.firestore()
          .collection("ENV")
          .get()
          .then(querySnapshot => {
            const ENVList = [];
            querySnapshot.forEach(doc => {
              ENVList.push(doc);
            });
            commit("setENV", ENVList);
          })
          .then(() => {
            resolve();
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    // Creating actions
    createClient: ({ dispatch }, newClient) => {
      return new Promise((resolve, reject) => {
        Firebase.firestore()
          .collection("clients")
          .add(newClient)
          .then(() => {
            console.log("Done creating client");
            dispatch("fetchClients").then(() => {
              resolve();
            });
          })
          .catch(error => {
            console.log("Error creating client: ", error);
          });
      });
    },
    createProject: ({ commit, dispatch }, newProject) => {
      commit("setLoading", { dataType: "page", status: true });
      Firebase.firestore()
        .collection("projects")
        .add(newProject)
        .then(() => {
          dispatch("setPageData", { type: "client", id: newProject.belongsTo });
        })
        .catch(error => {
          console.log("Error creating project: ", error);
          commit("setLoading", { dataType: "page", status: false });
        });
    },
    createRoom: ({ commit, dispatch }, newRoom) => {
      commit("setLoading", { dataType: "page", status: true });
      Firebase.firestore()
        .collection("rooms")
        .add(newRoom)
        .then(() => {
          dispatch("setPageData", { type: "project", id: newRoom.belongsTo });
        })
        .catch(error => {
          console.log("Error creating room: ", error);
          commit("setLoading", { dataType: "page", status: false });
        });
    },
    createMeasurement: ({ commit, dispatch }, newMeasurement) => {
      commit("setLoading", { dataType: "page", status: true });
      Firebase.firestore()
        .collection("measurements")
        .add(newMeasurement)
        .then(() => {
          dispatch("setPageData", { type: "room", id: newMeasurement.belongsTo });
        })
        .catch(error => {
          console.log("Error creating measurement: ", error);
          commit("setLoading", { dataType: "page", status: false });
        });
    },
    // Editing actions
    editClient: ({ commit, dispatch }, clientData) => {
      commit("setLoading", { dataType: "page", status: true });
      Firebase.firestore()
        .collection("clients")
        .doc(clientData.id)
        .set(clientData.body, { merge: true })
        .then(() => {
          if (!clientData.deleting) {
            dispatch("setPageData", { type: "client", id: clientData.id });
          }
        })
        .catch(error => {
          console.log("Error editing client: ", error);
          commit("setLoading", { dataType: "page", status: false });
        });
    },
    editProject: ({ commit, dispatch }, projectData) => {
      commit("setLoading", { dataType: "page", status: true });
      Firebase.firestore()
        .collection("projects")
        .doc(projectData.id)
        .set(projectData.body, { merge: true })
        .then(() => {
          if (!projectData.deleting) {
            dispatch("setPageData", { type: "project", id: projectData.id });
          }
        })
        .catch(error => {
          console.log("Error editing project: ", error);
          commit("setLoading", { dataType: "page", status: false });
        });
    },
    editRoom: ({ commit, dispatch }, roomData) => {
      commit("setLoading", { dataType: "page", status: true });
      Firebase.firestore()
        .collection("rooms")
        .doc(roomData.id)
        .set(roomData.body, { merge: true })
        .then(() => {
          if (!roomData.deleting) {
            dispatch("setPageData", { type: "room", id: roomData.id });
          }
        });
    }
  }
});



