<template>
  <div class="modal-box">
    <div class="modal-title">
      <h3>
        <base-icon iconRef="person" position="left"/>Bewerk klant
      </h3>
      <div class="flex-divider"></div>
      <base-button type="secondary" @click="closeModal">
        <base-icon iconRef="close"/>
      </base-button>
    </div>
    <div class="modal-content">
      <div class="modal-content-section">
        <div class="input-header">
          <h4>Basisgegevens</h4>
        </div>
        <div class="input-container">
          <label class="label" for>Klantnaam</label>
          <base-input type="text" v-model="clientBody.clientName"/>
        </div>
        <div class="input-container">
          <label for class="label">Beschrijving</label>
          <textarea class="input" v-model="clientBody.description"></textarea>
        </div>
      </div>
      <div class="modal-content-section">
        <div class="input-header">
          <h4>Contactpersoon</h4>
        </div>
        <div class="input-row">
          <div class="input-container">
            <label class="label">Voornaam</label>
            <base-input type="text" v-model="clientBody.contact.firstName"/>
          </div>
          <div class="input-container">
            <label class="label">Achternaam</label>
            <base-input type="text" v-model="clientBody.contact.lastName"/>
          </div>
        </div>
        <div class="input-container">
          <label class="label">Email adres</label>
          <base-input type="email" v-model="clientBody.contact.email"/>
        </div>
        <div class="input-container">
          <label class="label">Telefoonnummer</label>
          <base-input type="tel" v-model="clientBody.contact.tel"/>
        </div>
      </div>
    </div>
    <div class="modal-footer">
      <base-button
        @click="deleteClient"
        type="danger"
        :loading="deletingClient"
        :disabled="deletingClient"
      >Verwijderen</base-button>
      <div class="flex-divider"></div>
      <base-button @click="closeModal" type="secondary">Annuleren</base-button>
      <base-button @click="editClient" :loading="editingClient" :disabled="editingClient">
        <base-icon iconRef="checkmark" position="left"/>Bewerk
      </base-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "client-edit-modal",
  data: function() {
    return {
      deletingClient: false,
      editingClient: false,
      clientBody: {
        clientName: "",
        description: "",
        contact: {
          firstName: "",
          lastName: "",
          email: "",
          tel: ""
        }
      }
    };
  },
  computed: {
    clientData: function() {
      return this.$store.getters.getCurrentClient.data();
    }
  },
  methods: {
    editClient: function() {
      const clientData = {
        id: this.$route.params.clientId,
        body: this.$editClientDataObject(this.clientBody)
      };
      this.$store.dispatch("editClient", clientData);
      this.closeModal();
    },
    deleteClient: function() {
      const clientData = {
        id: this.$route.params.clientId,
        body: this.$deleteClientDataObject(),
        deleting: true
      };
      this.$store.dispatch("editClient", clientData);
      this.closeModal();
      this.$router.push(`/klanten`);
    },
    closeModal: function() {
      this.$store.commit("setModalShown", {
        dataType: "client",
        modalType: "edit",
        shown: false
      });
    }
  },
  mounted: function() {
    this.clientBody.clientName = this.clientData.clientName;
    this.clientBody.description = this.clientData.description;
    this.clientBody.contact.firstName = this.clientData.contact.firstName;
    this.clientBody.contact.lastName = this.clientData.contact.lastName;
    this.clientBody.contact.email = this.clientData.contact.email;
    this.clientBody.contact.tel = this.clientData.contact.tel;
  }
};
</script>



