<template>
  <div
    class="page-container"
    v-if="!pageLoading"
  >
    <base-breadcrumbs
      :parents="[['Home','/']]"
      :currentLocation="'Producten'"
    />
    <div class="page-box accent">
      <div class="page-box-title">
        <h2>Producten</h2>
      </div>
      <div class="page-box-datatable-container">
        <div class="data-table-wrapper">
          <table class="page-datatable">
            <thead>
              <tr>
                <th>Categorie</th>
                <th>Productnaam</th>
                <th class="align-right">Prijs</th>
              </tr>
            </thead>
            <tr
              v-for="product in productList"
              :key="product.id"
            >
              <td>{{product.field_125}}</td>
            </tr>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data: function() {
    return {
      placeholderImgUrl: "https://dummyimage.com/600x338/009ad9/009ad9&text=+",
      ENV: [],
      productList: []
    };
  },
  computed: {
    pageLoading: function() {
      return this.$store.state.loading.page;
    }
  },
  filters: {
    currencyFilter: function(value) {
      return " " + value.toFixed(2);
    }
  },
  created: function() {
    this.$store.commit("setLoading", { dataType: "page", status: true });
    this.$firestore
      .collection("ENV")
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          this.ENV.push(doc.data().value);
        });
      })
      .then(() => {
        const url =
          "https://api.knack.com/v1/objects/object_2/records?rows_per_page=1000";
        fetch(url, {
          headers: {
            "X-Knack-REST-API-KEY": this.ENV[0],
            "X-Knack-Application-Id": this.ENV[1]
          }
        })
          .then(knackResponse => {
            return knackResponse.json();
          })
          .then(value => {
            console.log(value);
            this.productAmount = value.total_records;
            this.productList = value.records;
            this.$store.commit("setLoading", {
              dataType: "page",
              status: false
            });
          });
      });
  }
};
</script>

<style scoped>
td,
th {
  padding-left: 12px !important;
  padding-right: 12px !important;
}
td {
  font-size: 14px !important;
}
.align-right {
  text-align: right;
}
</style>



