<template>
  <div class="page-container">
    <base-breadcrumbs
      :parents="[]"
      :currentLocation="'Home'"
    />
    <div class="page-box accent">
      <div class="page-box-title">
        <h2 class="page-box-heading">
          <span>
            <base-icon
              iconRef="people"
              position="left"
            />
            <span>Klanten</span>
          </span>
          <div class="page-box-sub-title">Klantgegevens, projecten, ruimtes en metingen.</div>
        </h2>
        <div class="flex-divider"></div>
        <router-link
          class="button primary"
          to="/klanten"
        >Bekijk
          <base-icon
            iconRef="arrow-forward"
            position="right"
          />
        </router-link>
      </div>
    </div>
    <div class="page-box accent">
      <div class="page-box-title">
        <h2 class="page-box-heading">
          <span>
            <base-icon
              iconRef="grid"
              position="left"
            />
            <span>Producten</span>
          </span>
          <div class="page-box-sub-title">Productgegevens, nog meer, en misschien nog iets anders.</div>
        </h2>
        <div class="flex-divider"></div>
        <router-link
          class="button primary"
          to="/producten"
        >Bekijk
          <base-icon
            iconRef="arrow-forward"
            position="right"
          />
        </router-link>
      </div>
      <div class="flex-divider"></div>
    </div>
  </div>
</template>

<script>
import Icon from "./Icon.vue";

export default {
  name: "",
  components: {
    Icon
  },
  data: function() {
    return {};
  },
  created: function() {
    this.$setPageTitle("Home");
  }
};
</script>

<style>
</style>



